.searchInput{
    margin-top: 118px;
    display: flex;
    align-items: center;
    position: relative;
    
}
.searchInput span {
    position: absolute;
    right: 11px;
    top: 50%;
    cursor: pointer;
    transform: translate(0%, -50%);
    display: inline-flex;
}

.searchInput input {
    border: 1px solid #CACACA!important;
    border-radius: 100px!important;
    box-shadow: 0px 15px 40px 0px #0000000d;
    height: 54px;
    font-size: 16px;
}

.searchbox {
    border: 1px solid #CACACA;
    border-radius: 50px;
    padding: 10px 10px 10px 55px;
    outline: none;
    width: 100%;
    /* background: #fff url('../../../assert/images/search.png') no-repeat 22px 18px; */
    box-sizing: border-box;
}
.searchbox::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    color:#848484;
  }
  .searchbox::-moz-placeholder { /* Firefox 19+ */
    color:#848484;
  }
  .searchbox:-ms-input-placeholder { /* IE 10+ */
    color:#848484;
  }
  .searchbox:-moz-placeholder { /* Firefox 18- */
    color:#848484;
  }